.site-list__item {
  position: relative;

  text-align: center;
  border-top: 1px solid fade(@white, 15%);
  border-bottom: 1px solid @black;

  @media (min-width: @desktop-width) {
    margin: 10px 0;
    padding: 0;
    border: 0;
  }

  &:not(:nth-child(4n+4)) {
    @media (min-width: @desktop-width) {
      margin-right: 80px;
    }
  }
}

.site-list__link {
  display: block;
  padding: 15px 20px;

  font-size: 18px;
  font-weight: 700;
  color: @white;

  text-transform: uppercase;
  text-decoration: none;

  @media (min-width: @desktop-width) {
    padding: 0;
    font-weight: 400;
    text-transform: none;
  }

  &[href]:hover {
    color: @purple-red;
  }

  &[href]:active {
    color: fade(@purple-red, 30%);

    @media (min-width: @desktop-width) {
      color: fade(@white, 30%);
    }
  }

  &--active::after {
    @media (min-width: @desktop-width) {
      content: "";
      position: absolute;
      height: 2px;
      width: 100%;
      top: 30px;
      left: 0;
      right: 0;
      background-color: @white;
    }
  }
}
